<template>
  <div id="note" class="detail">
    <NoteSidebar :notes.sync="notes"></NoteSidebar>
    <div class="note-detail">
 <div class="note-empty" v-show="!curBook.id">请创建笔记本后</div>
      <div class="note-empty" v-show="!curNote.id">选择或创建笔记</div>

      <div class="note-detail-ct" v-show="curNote.id">
        <div class="note-bar">
          <span>创建日期:{{ curNote.createdAtFriendly }}</span>
          <span>更新日期:{{ curNote.updatedAtFriendly }}</span>
          <span>{{ statusText }}</span>
          <span class="iconfont icon-delete" @click="onDeleteNote"></span>
          <span
            class="iconfont icon-fullscreen"
            @click="isShowPreview = !isShowPreview"
          ></span>
        </div>
        <div class="note-title">
          <input
            type="text"
            v-model="curNote.title"
            @input="onUpdateNotes"
            @keydown="statusText = '正在输入...'"
            placeholder="输入标题"
          />
        </div>
        <div class="editor">
          <textarea
            v-show="!isShowPreview"
            v-model="curNote.content"
            @input="onUpdateNotes"
            @keydown="statusText = '正在输入...'"
            placeholder="输入内容，支持 markdown 语法"
          ></textarea>
          <div class="preview markdown-body" v-html="previewContent" v-show="isShowPreview">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NoteSidebar from '@/components/NoteSidebar'
import _ from 'lodash'
import MarkdownIt from 'markdown-it'
import { mapGetters, mapMutations, mapActions } from 'vuex'


let md = new MarkdownIt()


export default {
  components: {
    NoteSidebar
  },
  
  data () {
    return {
      statusText: '笔记未改动',
      isShowPreview: false
    }
  },

  created() {
    this.checkLogin({ path: '/login' })
  },

  computed: {
    ...mapGetters([
      'notes',
    'curNote',
      'curBook'
      ]),

    previewContent() {
      return md.render(this.curNote.content||'')
    }
  },

  methods: {
    ...mapMutations([
      'setCurNote'
      ]),

    ...mapActions([
      'updateNote',
      'deleteNote',
      'checkLogin'
      ]),

    onUpdateNotes: _.debounce(function() {
      if(!this.curNote)return
      this.updateNote({ noteId: this.curNote.id, title: this.curNote.title, content: this.curNote.content })
        .then(data => {
          this.statusText = '已保存'
        }).catch(data => {
          this.statusText = '保存出错'
        })

    }, 300),

    onDeleteNote() {
      this.deleteNote({ noteId: this.curNote.id })
        .then(data => {
          this.$router.replace({ path: '/note' })
        })
    }
    
  },

  beforeRouteUpdate (to, from, next) {
    this.setCurNote({ curNoteId: to.query.noteId})
    next()
  }
}
</script>

<style lang="less">
@import url(../assets/note-detail.less);
  #note {
    display: flex;
    align-content: stretch;
    background-color: #fff;
    flex: 1;
  }
</style>



